* {
  margin: 0;
  padding: 0;
}

.service-standard {
  background-color: #e9e9e9;
  padding: 4px 0;
  position: sticky;
  left: 0;
  top: 0px;
  width: 100%;
  z-index: 99;
}

.service-standard-pc {
  display: flex;
  flex-wrap: nowrap;
  max-width: 1200px;
  margin: 0 auto;
  align-items: center;
  justify-content: space-between;
}

.service-standard-mb {
  visibility: hidden;
  height: 0;
}

.service-standard-item a {
  display: inline-block;
  text-decoration: none;
}

.service-standard-icon {
  width: 20px;
  height: 20px;
  display: inline-block;
  vertical-align: middle;
}

.service-standard-icon svg {
  display: inline-block;
  width: 100%;
  height: 100%;
  margin-top: -8px;
}

.service-standard-text {
  display: inline-block;
  vertical-align: middle;
  font-size: 14px;
  line-height: 1.58;
  color: #3a3a3a;
}

@media (max-width: 1399px) {
  .service-standard-content {
    width: 900px;
    margin: 0 auto;
  }
}

@media (max-width: 991px) {
  .service-standard {
    padding: 0 0 4px;
  }
  .service-standard-content {
    width: 100%;
  }
  .service-standard-mb {
    visibility: visible;
    height: auto;
  }
  .service-standard-mb .service-standard-item {
    text-align: center;
  }
  .service-standard-pc {
    display: none;
  }
  .service-standard-text {
    font-size: 12px;
  }
  .service-standard .slick-arrow {
    display: none !important;
  }
}
